/************************************** bubble Noticer ***************************************/
@keyframes slideInRight {
  from {
    right: -230px;
  }
  to {
    right: 0;
  }
}
.bubbleNoticer {
  color: #666;
  font-weight: bold;
  position: fixed;
  right: 0;
  padding: 10px;
  width: 150px;
  min-height: 15px;
  line-height: 15px;
  font-size: 15px;
  z-index: 1;
  opacity: 0.8;
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
  animation-duration: 0.5s;
  animation-name: slideInRight;
  transition: top 0.5s;
  word-break: break-all;
}
.bubbleNoticer::before {
  font: 100%/1 FontAwesome;
  margin-right: 10px;
}